"use client"
import React, { useState } from "react";
import { useCart } from "../..//components/CartContext";
import { useRouter } from "next/router";
import toast from "react-hot-toast";

const CartPage = () => {
    const router = useRouter();
    const { cart, removeFromCart, addToCart } = useCart();
    const [isProcessing, setIsProcessing] = useState(false);

    const handleIncreaseQuantity = (productId: string) => {
        const item = cart.find((i) => i.productId === productId);
        if (item) {
            addToCart({ ...item, quantity: 1 });  // Increase by 1
        }
    };

    const handleDecreaseQuantity = (productId: string) => {
        const item = cart.find((i) => i.productId === productId);
        if (item) {
            if (item.quantity > 1) {
                addToCart({ ...item, quantity: -1 });  // Decrease by 1
            } else {
                removeFromCart(productId);  // Remove item if quantity reaches 1
            }
        }
    };

    const handleRemoveItem = (productId: string) => {
        removeFromCart(productId);
    };

    const handlePlaceOrder = () => {
        // Check if user is logged in
        const userLoggedIn = localStorage.getItem("user"); // Example, replace with actual logic

        if (!userLoggedIn) {
            toast.error("Please log in first!");
            return;
        }

        // Show modal to confirm order
        // Call the placeOrder function once confirmed (we'll implement modal next)
    };

    return (
        <div className="max-w-7xl mx-auto p-6">
            <h2 className="text-3xl font-bold text-gray-900 mb-6">Your Cart</h2>
            <div className="space-y-6">
                {cart.map((item) => (
                    <div key={item.productId} className="flex items-center justify-between p-4 bg-gray-50 rounded-lg shadow-md">
                        <div className="flex items-center">
                            <img src={item.image} alt={item.name} className="w-16 h-16 object-cover rounded-lg" />
                            <div className="ml-4">
                                <h3 className="text-lg font-semibold">{item.name}</h3>
                                <p className="text-sm text-gray-500">${item.price}</p>
                            </div>
                        </div>
                        <div className="flex items-center space-x-2">
                            <button onClick={() => handleDecreaseQuantity(item.productId)} className="px-3 py-1 bg-gray-300 rounded-full">-</button>
                            <span>{item.quantity}</span>
                            <button onClick={() => handleIncreaseQuantity(item.productId)} className="px-3 py-1 bg-gray-300 rounded-full">+</button>
                            <button onClick={() => handleRemoveItem(item.productId)} className="ml-4 px-3 py-1 bg-red-500 text-white rounded-full">Remove</button>
                        </div>
                    </div>
                ))}
            </div>

            {/* Place Order Button */}
            <div className="mt-6">
                <button
                    onClick={handlePlaceOrder}
                    className="w-full py-3 px-6 bg-blue-600 text-white text-lg font-semibold rounded-lg hover:bg-blue-700 transition duration-300"
                >
                    Place Order
                </button>
            </div>
        </div>
    );
};

export default CartPage;
